import React from 'react';
import './MainContents.css';

function MainContents({page}){
  switch(page)
  {
    case '01' :
    return (
      <div className="HomeMain">
        <div className="HomeMain__mainSlide"></div>
          <h2>HOME</h2>
      </div>
    );
    case '02':
      return (
        <div className="MainContents">
          <h2>방송</h2>
        </div>
      );
    case '03':
      return (
        <div className="MainContents">
          <h2>LIVE</h2>
        </div>
      );
    case '04':
      return (
        <div className="MainContents">
          <h2>영화</h2>
        </div>
      );  
    case '05':
      return (
        <div className="MainContents">
          <h2>해외시리즈</h2>
        </div>
      ); 
    default :
      return (
        <div className="HomeMain">
          <div className="HomeMain__mainSlide"></div>
            <h2>HOME</h2>
        </div>
      );
  }
};


export default MainContents;
